@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/lato-regular.woff2") format("woff2"), url("../fonts/lato/lato-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswald-regular.woff2") format("woff2"), url("../fonts/oswald/oswald-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswald-medium.woff2") format("woff2"), url("../fonts/oswald/oswald-regular.woff") format("woff");
  font-display: swap;
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
  }
}
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.maps-no-js {
  transform: scale(0);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

.page {
  margin: 0 auto;
}

.page__body {
  min-height: 100%;
  margin: 0 auto;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}

.additional-products {
  position: relative;
}

.additional-products__wrapper {
  max-width: 1220px;
  margin: 0 auto;
  margin-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 35px;
}
@media (min-width: 768px) {
  .additional-products__wrapper {
    position: relative;
    margin-top: 68px;
    padding-left: 30px;
    padding-bottom: 25px;
  }
}
@media (min-width: 1440px) {
  .additional-products__wrapper {
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: 1fr 4fr;
    width: 1220px;
    margin-top: 80px;
    margin-bottom: 58px;
    padding-left: 0;
    padding-right: 0;
  }
}

.additional-products__title {
  display: inline-block;
  height: min-content;
  padding: 0 20px;
  margin: 0;
  margin-left: -20px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .additional-products__title {
    margin: 0;
    padding-left: 37px;
    padding-right: 36px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .additional-products__title {
    grid-column: 1/3;
    width: max-content;
    max-width: 1220px;
    margin-left: -40px;
    padding-left: 40px;
  }
}

.additional-products::before {
  content: "";
  position: absolute;
  top: 16px;
  z-index: -1;
  width: 100%;
  height: 2px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .additional-products::before {
    top: 20px;
  }
}

.additional-products__list {
  position: relative;
  width: 100%;
  margin: 0;
  margin-top: 50px;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .additional-products__list {
    width: auto;
    margin-top: 78px;
    margin-right: 8px;
  }
}
@media (min-width: 1440px) {
  .additional-products__list {
    margin-top: 37px;
    margin-right: 20px;
  }
}

.additional-products__list::before {
  content: "";
  position: absolute;
  top: -15px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}
@media (min-width: 768px) {
  .additional-products__list::before {
    display: none;
  }
}

.additional-products__item {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  margin-top: 33px;
}
@media (min-width: 768px) {
  .additional-products__item {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, min-content);
    width: 100%;
    margin: 0 auto;
    padding: 17px 40px 26px 38px;
    box-sizing: border-box;
    border: 1px solid #cdcdcd;
  }
}
@media (min-width: 1440px) {
  .additional-products__item {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    padding: 0;
    padding-top: 13px;
    border-left: none;
    border-right: none;
    border-bottom: none;
  }
}

@media (min-width: 768px) {
  .additional-products__item:not(:last-child) {
    border-bottom: none;
  }
}
@media (min-width: 1440px) {
  .additional-products__item:last-child {
    border-bottom: 1px solid #cdcdcd;
  }
}
.additionally {
