  padding-right: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .additionally__title {
    margin-left: 30px;
