    padding-right: 40px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .additionally__title {
    margin-left: calc(50% - 650px);
  }
}

.additionally__list {
  display: grid;
  margin: 0;
  margin-left: 20px;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .additionally__list {
    grid-template-columns: repeat(2, 50%);
    margin-left: 70px;
    margin-right: 10px;
  }
}
