  .additionally__list {
    display: grid;
    grid-template-columns: repeat(4, 26.6%);
    max-width: 1220px;
    margin-left: auto;
    margin-right: auto;
  }
}

.additionally__item {
  margin-top: 7px;
  margin-bottom: 2px;
}
@media (min-width: 768px) {
  .additionally__item {
    margin-top: 10px;
    margin-bottom: 12px;
  }
}
@media (min-width: 1440px) {
  .additionally__item {
    margin-top: 12px;
  }
}

.additionally__control {
  position: relative;
  display: block;
  margin-bottom: 13px;
  padding-left: 46px;
}
@media (min-width: 768px) {
  .additionally__control {
    margin-bottom: 20px;
    padding-left: 60px;
  }
}

.additionally__control-mark {
  position: absolute;
  top: -7px;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .additionally__control-mark {
    width: 38px;
    height: 38px;
  }
}

.additionally__control:hover .additionally__control-mark {
  border-color: #bababa;
}

.additionally__control-input[type=checkbox]:focus + span {
  border-color: #444444;
  outline: 1px solid #444444;
}

.additionally__control-input:disabled + .additionally__control-mark {
  border-color: #bababa;
  opacity: 0.3;
}

.additionally__control-input[type=checkbox]:checked + .additionally__control-mark::before,
.additionally__control-input[type=checkbox]:checked + .additionally__control-mark::after {
  position: absolute;
  content: url("../img/checkbox-active.svg");
  top: 7px;
  left: 7px;
}
@media (min-width: 768px) {
  .additionally__control-input[type=checkbox]:checked + .additionally__control-mark::before,
.additionally__control-input[type=checkbox]:checked + .additionally__control-mark::after {
    top: 12px;
    left: 12px;
    transform: scale(1.3);
  }
}

.additionally__control-label {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .additionally__control-label {
    font-size: 20px;
    line-height: 30px;
  }
}

